<template>
  <div>
    <!-- 首页
        当前数字是{{count}}
        <button @click='add'>add</button>
        <button @click='remove'>reduce</button>
    <button @click='add2'>异步增加</button>-->
    <myswiper></myswiper>
    <hotbook></hotbook>
  </div>
</template>

<script>
import myswiper from './swiper'
import hotbook from './hot-book'
export default {
  //相当于以前let组件时的对象，只不过不需要再写template,会默认去找这个页面上面的template
  data() {
    return {
      name: "",
    };
  },
  methods: {
    add() {
      //调用mutations中的add函数
      this.$store.commit("add");
    },
    remove() {
      this.$store.commit("remove");
    },
    add2() {
      this.$store.dispatch("addFn");
    }
  },
  components: {
      myswiper,
      hotbook
  }
};
</script>

